    function hideTabContent() {
        tabsContent.forEach(item => {
            //// item.style.display = 'none'; //? –∑–∞–º–µ–Ω—è–µ–º inline —Å—Ç–∏–ª–∏
            //*–∫–ª–∞—Å—Å—ã –¥–æ–ø–∏—Å–∞–Ω—ã –≤ _preview.scss

            item.classList.add('hide');
            item.classList.remove('show', 'fade');
        });

        //*—É–±–µ—Ä–µ–º –∫–ª–∞—Å—Å –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ —É —Ç–∞–±–æ–≤

        tabs.forEach(item => {
            item.classList.remove('tabheader__item_active');
        });
    }

    /* function showTabContent (i) {            //?–Ω—É–∂–Ω–æ –∑–Ω–∞—Ç—å –∫ –∫–∞–∫–æ–º—É —ç–ª–µ–º–µ–Ω—Ç—É –º—ã –æ–±—Ä–∞—â–∞–µ–º—Å—è –ø–æ—ç—Ç–æ–º—É –ø–µ—Ä–µ–¥–∞–µ–º –∞—Ä–≥—É–º–µ–Ω—Ç (i)
        tabContent[i].style.display = 'block';
        tabs[i].classList.add('tabheader__item_active');
    } */

    function showTabContent(i = 0) {
        ////tabsContent[i].style.display = 'block';            //? –∑–∞–º–µ–Ω—è–µ–º inline —Å—Ç–∏–ª–∏
        tabsContent[i].classList.add('show', 'fade');
        tabsContent[i].classList.remove('hide');
        tabs[i].classList.add('tabheader__item_active');


    }

    hideTabContent();
    /* showTabContent(0); */
    showTabContent();

    tabsParent.addEventListener('click', (event) => {
        const target = event.target;

        if (target && target.classList.contains('tabheader__item')) {
            tabs.forEach((item, i) => {
                if (target == item) {
                    hideTabContent();
                    showTabContent(i);
                }
            });
        }
    });

    //Todo TIMER task

    const deadLine = '2021-07-20';

    //* —Ä–∞–∑–Ω–∏—Ü–∞ –º–µ–∂–¥—É deadLine –∏ —Ç–µ–∫—É—â–µ–π –¥–∞—Ç–æ–π

    function getTimeRemaining(endTime) {
        const t = Date.parse(endTime) - Date.parse(new Date()),
            days = Math.floor((t / (1000 * 60 * 60 * 24))), // –æ–±—â–µ–µ –∫–æ–ª-–≤–æ –º–∏–ª–∏—Å–∏–∫—É–Ω–¥ / (–º–∏–ª–∏—Å–µ–∫—É–Ω–¥ –≤ —Å—É—Ç–∫–∞—Ö) = —Å–∫–æ–ª—å–∫–æ —Å—É—Ç–æ–∫ –æ—Å—Ç–∞–ª–æ—Å—å –¥–æ —Ç–µ–∫—É—â–µ–π –¥–∞—Ç—ã math.–æ–∫—Ä—É–≥–ª–µ–Ω–∏–µ
            hours = Math.floor((t / (1000 * 60 * 60) % 24)), // –æ–±—â–µ–µ –∫–æ–ª-–≤–æ –º–∏–ª–∏—Å–∏–∫—É–Ω–¥ / (–º–∏–ª–∏—Å–µ–∫—É–Ω–¥ –≤ —á–∞—Å–µ)
            minutes = Math.floor((t / 1000 / 60) % 60),
            seconds = Math.floor((t / 1000) % 60);

            return {
            // https://alligator.io/js/object-property-shorthand-es6/
            'total': t,
            'days': days,
            'hours': hours,
            'minutes': minutes,
            'seconds': seconds
        };
    }

    function getZero(num) {
        if (num >= 0 && num < 10) {
            return `0${num}`;
        } else {
            return num;
        }
    }

    //* —Ñ-—Ü–∏—è —É—Å—Ç–∞–Ω–æ–≤–∫–∏ —Ç–∞–π–º–µ—Ä–∞ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É
    function setClock(selector, endTime) {

        const timer = document.querySelector(selector),
            days = timer.querySelector('#days'),
            hours = timer.querySelector('#hours'),
            minutes = timer.querySelector('#minutes'),
            seconds = timer.querySelector('#seconds'),
            timeInterval = setInterval(updateClock, 1000);

        updateClock(); // —É–±–∏—Ä–∞–µ—Ç –±–∞–≥ (default date set in HTML) –º–∏–≥–∞–Ω–∏—è –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ —Å—Ç—Ä–∞–Ω–∏—Ü—ã

        function updateClock() {
            const t = getTimeRemaining(endTime); // —Ä–∞—Å—á–µ—Ç –æ—Å—Ç–∞–≤—à–µ–≥–æ—Å—è –≤—Ä–µ–º–µ–Ω–∏

            /* days.innerHTML = t.days;
                hours.innerHTML = t.hours;
                minutes.innerHTML = t.minutes;
                seconds.innerHTML = t.seconds; */

            days.innerHTML = getZero(t.days);
            hours.innerHTML = getZero(t.hours);
            minutes.innerHTML = getZero(t.minutes);
            seconds.innerHTML = getZero(t.seconds);


            if (t.total <= 0) {
                clearInterval(timeInterval);
                days.innerHTML = 0;
                hours.innerHTML = 0;
                minutes.innerHTML = 0;
                seconds.innerHTML = 0;
            }
        }
    }
    setClock('.timer', deadLine);

