    //! –ï–°–õ–ò –í–ê–® –ö–û–î –ü–û–í–¢–û–†–Ø–ï–¢–°–Ø, –¢–û –ï–°–¢–¨ –°–ú–´–°–õ –í–´–ù–ï–°–¢–ò –î–ê–ù–ù–´–ï –°–¢–†–û–ö–ò –í –û–¢–î–ï–õ–¨–ù–£–Æ –§–£–ù–ö–¶–ò–Æ 

